body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  background-color: #f1f2f5;
  /* height:100vh; */
}

.appBody{
  display:flex;
}
.header {
  display: flex;
  padding: 15px 20px;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 100;
  top: 0;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.headerLeft{
 display: flex;
 justify-content: space-evenly;

}
.headerLeft > img{
  height: 40px;
}
.headerInput{
  display: flex;
  align-items: center;
  background-color: #eff2f5;
  padding: 10px;
  margin-left: 10px;
  border-radius: 999px;

}
.headerInput > input{
  border: none;
  background-color: transparent;
  outline-width: 0;
}
.headerMiddle{
   display:flex;
   flex:1 1;
   justify-content: center;

}
.headerOption{
  display: flex;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
}
.headerOption:hover{
  background-color: #eff2f5;
  border-radius: 10px;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
  border-bottom: none;
}
.headerOption > .MuiSvgIcon-root{
  color:gray
}
.headerOption:hover > .MuiSvgIcon-root{
  color:#2e81f4
}
.headerOptionActive{
  border-bottom: 4px solid #2e81f4 ;
}
.headerOptionActive .MuiSvgIcon-root{
  color:#2e81f4
}
.headerInfo{
  display: flex;
  align-items:center;
}
.headerInfo >h4{
 margin-left: 10px;
}
.headerRight{
  display: flex;
}
.sidebarRow{
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}
.sidebarRow:hover{
  background-color: lightgray;
  border-radius: 10px;
}
.sidebarRow > h4{
  margin-left: 10px;
  font-weight: 600;
}
.sidebarRow .MuiSvgIcon-root{
  font-size: xx-large;
  color:#2e81f4
}
.sidebar{
  padding: 25px 10px;
  flex:0.33 1;
  
}
.story{
  position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 120px;
  height: 200px;
  box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
  border-radius: 10px;
  margin-right: 10px;
  transition: transform 100ms ease-in;
  cursor: pointer;
}

.story:hover {
  transform: scale(1.07);
}

.storyAvatar{
  margin: 10px;
  border: 5px solid #2e81f4
}

.story > h4{
  position: absolute;
  bottom: 20px;
  left:20px;
  color:white
}
.storyReel{ 
  display: flex;
}
.feed{
  flex: 1 1;
  padding: 30px 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.messageSender{
  display:flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  width:100%
}

.messageSenderTop{
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding-bottom: 15px;
  padding: 15px;
}
.messageSenderTop > form {
  flex: 1 1;
  display: flex;
}

.messageSenderTop > form > input{
   outline-width: 0;
   border:none;
   padding: 5px 20px;
   margin: 0px 10px;
   border-radius: 999px;
   background-color: #eff2f5;
}
.messageSenderTop > form > button{
  display: none
}

.messageSenderInput{
  flex: 1 1
}

.messageSenderBottom{
  display: flex;
  justify-content: space-evenly;
}
.messageSenderOption{
  padding: 20px;
  display: flex;
  align-items: center;
  color: gray;
  margin: 5px;
}
.messageSenderOption > h3{
  font-size: medium;
  margin-left: 10px;
  cursor: pointer;
}

.messageSenderOption:hover{
  background-color: #eff2f5;
  border-radius: 24px;
}

.post{
  width: 100%;
  margin-top:  15px;
  border-radius: 15px;
  background-color: white;
  box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
}

.postTop{
  display: flex;
  position: relative;
  align-items: center;
  padding: 15px;
}
.postTopInfo > h3 {
   font-size: medium;
}
.postTopInfo > p {
  font-size: small;
  color: gray;
}

.postBottom{
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 15px 25px;
}

.postAvatar{
  margin-right: 10px;
}

.postImage >img {
  width: 100%;
}

.postOptions {
  padding-top: 10px;
  border-top: 1px solid lightgray;
  display: flex;
  justify-content: space-evenly;
  font-size: medium;
  color: gray;
  cursor: pointer;
  padding: 15px;
}

.postOption {
  display:flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  flex: 1 1;
}

.postOption > p {
  margin-left: 10px;
}

.postOption:hover{
  background-color: #eff2f5;
  border-radius: 10px;
}

