.messageSender{
  display:flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  width:100%
}

.messageSenderTop{
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding-bottom: 15px;
  padding: 15px;
}
.messageSenderTop > form {
  flex: 1;
  display: flex;
}

.messageSenderTop > form > input{
   outline-width: 0;
   border:none;
   padding: 5px 20px;
   margin: 0px 10px;
   border-radius: 999px;
   background-color: #eff2f5;
}
.messageSenderTop > form > button{
  display: none
}

.messageSenderInput{
  flex: 1
}

.messageSenderBottom{
  display: flex;
  justify-content: space-evenly;
}
.messageSenderOption{
  padding: 20px;
  display: flex;
  align-items: center;
  color: gray;
  margin: 5px;
}
.messageSenderOption > h3{
  font-size: medium;
  margin-left: 10px;
  cursor: pointer;
}

.messageSenderOption:hover{
  background-color: #eff2f5;
  border-radius: 24px;
}
